import { PDFDownloadLink, PDFViewer } from '@react-pdf/renderer';
import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useLocation } from 'react-router-dom';
import { fetchConToken } from '../../helpers/fetch';
import { ClienteItemsDetalle } from '../clientes/ClienteItemsDetalle';
import { ComprobanteDetalle } from '../ui/ComprobanteDetalle';
import { Pagination } from '../ui/Pagination';
import moment from 'moment';
import Swal from 'sweetalert2';
import { ComprobanteDetalleClientes } from '../ui/ComprobanteDetalleClientes';
import { setAbierto, setFinalizado } from '../../actions/eventos';
import { isMobile } from 'react-device-detect';
import { EnviarMensajeCelu } from './EnviarMensajeCelu';
import { uiOpenModal } from '../../actions/ui';
import Select from 'react-select';

export const EventoDetalle = () => {
    const history = useHistory();
    const location = useLocation();
    const dispatch = useDispatch();
    const { uid } = useSelector((state) => state.auth);
    const { estadoWsp } = useSelector((state) => state.ui);
    const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
    const [planilla, setPlanilla] = useState(false);
    const [deuTotal, setDeuTotal] = useState(0);
    const [ganTotal, setGanTotal] = useState(0);
    const [ganPuerta, setGanPuerta] = useState(0);
    const { activeEvent } = useSelector((state) => state.eventos);
    const [cantidadClientes, setCantidadClientes] = useState(0);
    const [loading, setLoading] = useState(false);
    const [clienteCurrentPage, setClienteCurrentPage] = useState(1);
    const [clientes, setClientes] = useState([]);
    const [rrpps, setRRPPS] = useState([]);
    const [clientesPerPage, setClientesPerPage] = useState(10);
    const indexOfLastCliente = clienteCurrentPage * clientesPerPage;
    const indexOfFirstCliente = indexOfLastCliente - clientesPerPage;
    const currentClientes = clientes.slice(indexOfFirstCliente,indexOfLastCliente);
    //FETCHING
    useEffect(() => {
        const getClientesByEvento = async() =>{
            const resp = await fetchConToken(`clientes/evento/${idEvento}`);
            const body = await resp.json();
            setClientes(body.clientes);
            setPlanilla(true);
            setCantidadClientes(body.total);
        }
        getClientesByEvento();
        const getRRPPByEvento = async() =>{
            const resp = await fetchConToken(`auth/rrpps/${idEvento}`);
            const body = await resp.json();
            setRRPPS(body.rpps);
        }
        getRRPPByEvento();
        const getGanDeuByEvento = async() =>{
            const resp = await fetchConToken(`auth/eventos/deugan/${idEvento}`);
            const body = await resp.json();
            setDeuTotal(body.deutotal);
            setGanTotal(body.gantotal);
            setGanPuerta(body.ganpuerta);
        }
        getGanDeuByEvento();
        
    }, [idEvento]);
    const handleVolver = (e) => {
        history.push('/controlevento');
    };
    //ACCIONES
    const handleEditar = (evento) => {
        history.push(`/editar/${idEvento}`);
    };
    const handleListaFREE = (evento) => {
        history.push(`/listafree/${idEvento}`);
    };
    const handleAbrir = () => {
        dispatch(setAbierto(idEvento));
        setTimeout(()=>{
            history.push(`/controlevento`);
        },1000)
    };
    const handleFinalizar = () => {
        dispatch(setFinalizado(idEvento));
        setTimeout(()=>{
            history.push(`/controlevento`);
        },1000)
    };
    const handleLimpiarFREE = () => {
        const limpiarFREE = async() =>{
            const respu = await fetchConToken(`clientes/resetfree`,{uid},'POST');
            const body = await respu.json();
            if(body.ok){
                Swal.fire('Exito','Clientes FREE desasociados con exito','success');
            }
        }
        limpiarFREE();
    };
    const handleReset = () => {
        const getResetIngresos = async() =>{
            const respu = await fetchConToken(`clientes/resetingresos`);
            const body = await respu.json();
            if(body.ok){
                Swal.fire('Exito','Ingresos de clientes reseteados con exito','success');
            }
        }
        getResetIngresos();
    };
    const handlePasarPago = () => {
        const getPasarPago = async() =>{
            const respu = await fetchConToken(`clientes/pagotofree`);
            const body = await respu.json();
            if(body.ok){
                Swal.fire('Exito','Clientes pago pasados a Free exito','success');
            }
        }
        getPasarPago();
    };
    const handleEnviarWSPALL = () => {
        dispatch(uiOpenModal());
    };


    //BUSQUEDA
    const [values, setValues] = useState({cSearch:""});
    const handleSearchInputChange = ({ target }) => {
        const getClientesByEvento = async() =>{
            const resp = await fetchConToken(`clientes/evento/${idEvento}`);
            const body = await resp.json();
            setClientes(body.clientes);
            setPlanilla(true);
            setCantidadClientes(body.total);
        }
        if(target.value===""){
            getClientesByEvento();
        }
        setValues({
        ...values,
        [target.name]: target.value,
        });
    };
    const { cSearch } = values;
    const handleBuscar = (e) => {
        e.preventDefault();
        const searchCliente = async() =>{
            const resp = await fetchConToken(`clientes/searcht/${cSearch}/${idEvento}`);
            const body = await resp.json();
            setClientes(body.clientess);
        }
        searchCliente();
    };
    const handleFiltrarRRPP = (e) => {
        e.preventDefault();
        const filtrarRRP = async() =>{
            const resp = await fetchConToken(`clientes/rrpp/${rrpp.value}/evento/${idEvento}`);
            const body = await resp.json();
            setClientes(body.clientes);
        }
        filtrarRRP();
    };

    let rrppss = [];
    for (let i = 0; i < rrpps.length; i++) {
    const evento = {
      value:rrpps[i].uid,
      label:rrpps[i].nombre +" - "+rrpps[i].apellido,
    }
    rrppss.push(evento);
  }
    const [rrpp, setRRPP] = useState("");
    const handleRRPP = (e) => {
        setRRPP(e);
    };
  return (
    <div className='container-fluid'>
        <div>
            <div className="form-group d-flex justify-content-center">
                {activeEvent && <h3 className='text-dark'><b>{activeEvent.nombre}</b></h3>}
            </div>
            <div className="d-flex flex-row justify-content-evenly align-items-center">
                {activeEvent &&<PDFDownloadLink
                    document={ <ComprobanteDetalle datos={{gan:ganTotal+ganPuerta-deuTotal,deuTotal:deuTotal, ganTotal:ganTotal, ganPuerta:ganPuerta, evento:activeEvent.nombre, fecha:moment(activeEvent.fecha).format('DD-MM-yyyy'),cantCli:clientes.length}}/>}
                    fileName="detalle.pdf"
                    className="text-decoration-none"
                >
                    <div className="form-group d-flex justify-content-center">
                        <button className="btn btn-outline-primary mt-2 mb-2" type="submit"><i className="fas fa-print"></i> Imprimir detalle</button>
                    </div>
                </PDFDownloadLink>}
                {activeEvent && planilla &&<PDFDownloadLink
                    document={ <ComprobanteDetalleClientes evento={idEvento} clientes={clientes} datos={{ evento:activeEvent.nombre, fecha:moment(activeEvent.fecha).format('DD-MM-yyyy')}}/>}
                    fileName="planilla-clientes.pdf"
                    className="text-decoration-none"
                >
                    <div className="form-group d-flex justify-content-center">
                        <button className="btn btn-outline-primary mt-2 mb-2" type="submit"><i className="fas fa-print"></i> Imprimir planilla clientes</button>
                    </div>
                </PDFDownloadLink>}
                 {/* {activeEvent && planillaRrpp &&<PDFViewer width={500} height={500}>
                    <ComprobanteDetalleRRPP rrpps={rpps} datos={{ evento:activeEvent.nombre, fecha:moment(activeEvent.fecha).format('DD-MM-yyyy'),cantRrpp:rpps.length}}/>
                </PDFViewer>} */}
                </div>
                <div className="d-flex flex-column justify-content-center align-items-center">
                <div className="dropdown">
                    <button className="btn btn-primary dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                        <i className="fas fa-cog"></i> Acciones
                    </button>
                    <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                        <div className='d-grid gap-1'>
                            {activeEvent&&activeEvent.finalizado?<button className="btn btn-success" onClick={()=>{handleAbrir()}}><i className="fas fa-unlock"></i> Abrir Evento</button>
                            :<button className="btn btn-danger" onClick={()=>{handleFinalizar()}}><i className="fas fa-lock"></i> Cerrar Evento</button>}
                            <button className="btn btn-primary" onClick={()=>{handleLimpiarFREE()}}><i className="fas fa-broom"></i> Limpiar FREE</button>
                            {/* <button className="btn btn-primary" onClick={()=>{handlePasarPago()}}><i className="fas fa-arrow-right"></i> Pagos a FREE</button> */}
                            <button className="btn btn-primary" onClick={()=>{handleReset()}}><i className="fas fa-arrow-rotate-left"></i> Reset Ingreso</button>
                            <button className="btn btn-success" onClick={handleListaFREE}><i className="fas fa-list"></i> Lista FREE</button>
                            <button className="btn btn-warning" onClick={handleEditar}><i className="fas fa-edit"></i> Editar</button>
                            {estadoWsp === "Conectado"&&<button className="btn btn-success" onClick={handleEnviarWSPALL}><i className="fas fa-arrow-up"></i> Mandar WSP a todos</button>}
                        </div>
                    </ul>
                </div>
            </div>
        </div>
    <div className="d-flex flex-row justify-content-between align-items-center mb-3">
        <div className='form-group text-center me-2 w-25'>
          <label><b>Deuda a RRPPs</b></label>
          <input disabled={true} className='form-control' type="text" value={loading?'Cargando...':`$ ${deuTotal?deuTotal:0}`}/>
        </div>
        <div className='form-group text-center me-2 w-25'>
          <label><b>Ingreso de anticipadas</b></label>
          <input disabled={true} className='form-control' type="text" value={loading?'Cargando...':`$ ${ganTotal}`}/>
        </div>
        <div className='form-group text-center me-2 w-25'>
          <label><b>Ingreso por puerta</b></label>
          <input disabled={true} className='form-control' type="text" value={loading?'Cargando...':`$ ${ganPuerta}`}/>
        </div>
    </div>
    <div className='d-flex justify-content-center mb-2'>
        <div className='form-group text-center me-2 w-25 form-group'>
          <label><b>Ganancia total</b></label>
          <input disabled={true} className='form-control' type="text" value={loading?'Cargando...':`$ ${ganTotal+ganPuerta-deuTotal}`}/>
        </div>
    </div>
    
    {isMobile
    ?
    <div className='d-flex flex-column'>
        <div className='d-flex'>
            <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleBuscar}>
                <input className='form-control me-2 text-center' title='Busqueda por apellido' placeholder='Ingrese apellido o DNI completo' type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
                <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
            </form>
        </div>
        <div className='d-flex'>
            <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleFiltrarRRPP}>
            {/* <label className='form-label me-2'><b>Filtro por RRPP</b></label> */}
                <Select className='w-50 me-2' options={rrppss} value={rrpp} onChange={handleRRPP}/>
                <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
            </form>
        </div>
    </div>
    :
    <div className='d-flex'>
    <div className='d-flex justify-content-lg-end w-25'></div>
        <div className='d-flex justify-content-lg-end w-100'>
            <form className='form-group d-flex justify-content-center align-items-center mb-2 w-50' onSubmit={handleBuscar}>
                <input className='form-control me-2 text-center' title='Busqueda por apellido' placeholder='Ingrese apellido o DNI completo' type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
                <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
            </form>
        </div>
        <div className='d-flex justify-content-lg-end w-75'>
            <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleFiltrarRRPP}>
            {/* <label className='form-label me-2'><b>Filtro por RRPP</b></label> */}
                <Select className='w-50 me-2' options={rrppss} value={rrpp} onChange={handleRRPP}/>
                <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
            </form>
        </div>
    </div>
    }
    
    {isMobile?<div>
                <div className='d-flex justify-content-end align-items-center flex-column'>
                    <h3 className='text-dark'><b>Clientes</b></h3>
                    <h4>{cantidadClientes}</h4>
                </div>
                    <ClienteItemsDetalle clientes={currentClientes} loading={loading}/>
                    <Pagination dataPerPage={clientesPerPage} totalData={clientes.length} data={clientes} setCurrentPage={setClienteCurrentPage}/>
                
    </div>
    :<div className='form-group d-flex justify-content-center align-items-start flex-row mb-1'>
                <div className='col-9 d-flex justify-content-end align-items-center flex-column'>
                    <h3 className='text-dark'><b>Clientes</b></h3>
                    <h4>{cantidadClientes}</h4>
                    <ClienteItemsDetalle clientes={currentClientes} loading={loading}/>
                    <Pagination dataPerPage={clientesPerPage} totalData={clientes.length} data={clientes} setCurrentPage={setClienteCurrentPage}/>
                </div>
    </div>}
    <EnviarMensajeCelu />
    <div className="form-group d-flex justify-content-center">
        <button className="btn btn-primary mb-5" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
    </div>
</div>
  );
};
