import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import Select from 'react-select';
import { uiCloseModalll, uiCloseModalllll } from "../../actions/ui";
import { eventSetActive, setEventosByRpp } from "../../actions/eventos";
import "../modal.css";
import { useHistory } from "react-router-dom";
import { startDesasociarClienteSinEvento } from "../../actions/clientes";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const AsociarEventoCeluModal = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const { modalOpennnnn } = useSelector((state) => state.ui);
  const { uid } = useSelector((state) => state.auth);
  const { eventos } = useSelector((state) => state.eventos);
  const { clienteAsoc } = useSelector((state) => state.clientes);
  let eventoss = [];
  for (let i = 0; i < eventos.length; i++) {
    const evento = {
      value:eventos[i].id,
      label:eventos[i].nombre
    }
    eventoss.push(evento);
  }
  const [evento, setEvento] = useState("");
  useEffect(() => {
    dispatch(setEventosByRpp(uid));
  }, [dispatch,uid]);

  const handleEvento = (e) => {
    setEvento(e);
  };
  const closeModalllll = () => {
    dispatch(uiCloseModalllll());
    // dispatch(eventClearIsFilter());
  };
  const handleCelu= () => {
    // dispatch(eventIsFilter());
    dispatch(eventSetActive(evento.value));
    closeModalllll();
    // dispatch(startDesasociarClienteSinEvento(clienteAsoc.uid,uid,evento.value));
    // setTimeout(()=>{
    //   history.push(`/`);
    // },500);
    
    
  };
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpennnnn}
        onRequestClose={closeModalllll}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      > 
        <h1 className="black">Elegir evento a cambiar numero de celular</h1>
        <hr />
        <div className="form-control">
           <Select options={eventoss} value={evento} onChange={handleEvento}/>
        </div>
        <div className="mt-5">
        <button onClick={()=>handleCelu()} className="btn btn-outline-primary btn-block">
          <i className="fas fa-arrow"></i>
          <span>Cambiar</span>
        </button>
        </div>
      </Modal>
    </div>
  );
};
