import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import Select from 'react-select';
import { uiCloseModallll } from "../../actions/ui";
import { setEventosByRpp } from "../../actions/eventos";
import "../modal.css";
import Swal from "sweetalert2";
import { fetchConToken } from "../../helpers/fetch";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const AsociarReenviarWSPModal = () => {
  const dispatch = useDispatch();
  const { modalOpennnn } = useSelector((state) => state.ui);
  const { uid } = useSelector((state) => state.auth);
  const { eventos } = useSelector((state) => state.eventos);
  const { clienteAsoc } = useSelector((state) => state.clientes);
  let eventoss = [];
  for (let i = 0; i < eventos.length; i++) {
    const evento = {
      value:eventos[i].id,
      label:eventos[i].nombre
    }
    eventoss.push(evento);
  }
  const [evento, setEvento] = useState("");
  useEffect(() => {
    dispatch(setEventosByRpp(uid));
  }, [dispatch,uid]);

  const handleEvento = (e) => {
    setEvento(e);
  };
  const closeModallll = () => {
    dispatch(uiCloseModallll());
  };
  const handleFiltrar = () => {
    closeModallll();
    const reenviar = async()=>{
      const resp = await fetchConToken(`clientes/reenviarwsp/${clienteAsoc.uid}/${evento.value}`);
      const body = await resp.json();
      if(body.ok){
        Swal.fire({title:"PDF a WSP",text:"¿Seguro que desea reenviar el PDF por WSP?", icon:"question",confirmButtonText: 'OK. Abrir WSP WEB'}).then((result)=>{
          if(result.isConfirmed){
            window.open(`https://wa.me/549${body.celular}?text=https://event-a.herokuapp.com/descargapdf/${evento.value}/${clienteAsoc.uid}`);
          }
        });
      }
    }
    reenviar();
    
    
  };
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpennnn}
        onRequestClose={closeModallll}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      > 
        <h1 className="black">Elegir evento de la entrada a reenviar WSP</h1>
        <hr />
        <div className="form-control">
           <Select options={eventoss} value={evento} onChange={handleEvento}/>
        </div>
        <div className="mt-5">
        <button onClick={()=>handleFiltrar()} className="btn btn-outline-success btn-block">
          <i className="fa-brands fa-whatsapp"></i>
          <span> Reenviar</span>
        </button>
        </div>
      </Modal>
    </div>
  );
};
