import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import Select from 'react-select';
import { uiCloseModal } from "../../actions/ui";
import { Asociar, startClearUserAsoc } from "../../actions/auth";
import { setEventos, setEventosByRpp } from "../../actions/eventos";
import "../modal.css";
import { useForm } from "../../hooks/useForm";
import moment from 'moment';
import { fetchConToken } from "../../helpers/fetch";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const AsociarModal = () => {
  const dispatch = useDispatch();
  const { modalOpen } = useSelector((state) => state.ui);
  const { userAsoc,uid } = useSelector((state) => state.auth);
  const [rrpps, setRRPPS] = useState();
  const [rrpp, setRRPP] = useState("");
  const [rol, setRol] = useState("");
  const { eventos } = useSelector((state) => state.eventos);
  let eventoss = [];
  for (let i = 0; i < eventos.length; i++) {
    const evento = {
      value:eventos[i].id,
      label:eventos[i].nombre+" - "+moment(eventos[i].fecha).format('DD/MM/YYYY')
    }
    eventoss.push(evento);
  }
  const [evento, setEvento] = useState("");
  let rrppss = [];
  if(rrpps){
    for (let i = 0; i < rrpps.length; i++) {
      const rrpp = {
        value:rrpps[i].uid,
        label:rrpps[i].nombre+" "+rrpps[i].apellido
      }
      rrppss.push(rrpp);
    }
  }
  let roles = [
    { value: "RRPP", label: "RRPP" },
    { value: "TARJETERO", label: "TARJETERO" },
    { value: "ESPECIAL", label: "ESPECIAL" }
  ];
  // const { eventos } = useSelector((state) => state.eventos);
  // let eventoss = [];
  // for (let i = 0; i < eventos.length; i++) {
  //   const evento = {
  //     value:eventos[i].id,
  //     label:eventos[i].nombre
  //   }
  //   eventoss.push(evento);
  // }
  // const [evento, setEvento] = useState("");

  const [formRegisterValues, handleRegisterInputChange] = useForm({
    rComision:"",
    rComisionTarjetero:"",
    rCantidadFree:""
});
const { rComision,rComisionTarjetero,rCantidadFree } = formRegisterValues;

  useEffect(() => {
    dispatch(setEventos());
  }, [dispatch]);
  // useEffect(() => {
  //   dispatch(setEventosByRpp(uid));
  // }, [dispatch,uid]);

  const handleRol = (e) => {
    setRol(e);
};
const handleEvento = (e) => {
setEvento(e);
const getRppsByEvento = async() =>{
  const resp = await fetchConToken(`auth/rrppsparaasociar/${e.value}`);
  const body = await resp.json();
  setRRPPS(body.rpps);
}
getRppsByEvento();
};
const handleRRPP = (e) => {
setRRPP(e);
};
  const closeModal = () => {
    dispatch(uiCloseModal());
    dispatch(startClearUserAsoc());
  };
  const handleAsociar = () => {
    dispatch(Asociar(userAsoc, evento,rol,rrpp,rComision,rComisionTarjetero,rCantidadFree));
    closeModal();
    // window.location.reload();
  };
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpen}
        onRequestClose={closeModal}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      > 
        <h1 className="black d-flex justify-content-center">Elegir evento</h1>
        <div className="form-group text-center mb-2">
    <label className='form-label'><b>Evento</b></label>
    <div className="form-control mb-2">
           <Select options={eventoss} value={evento} onChange={handleEvento}/>
        </div>
    </div>
    <div className="form-group mb-2">
      <input
        type="number"
        title="Comision"
        className="form-control"
        placeholder="Comision de Entradas de cada RRPP o TARJETERO"
        name="rComision"
        value={rComision}
        onChange={handleRegisterInputChange}
      />
    </div>
    <div className="form-group mb-2">
      <input
        type="number"
        title="Cantidad Free"
        className="form-control"
        placeholder="Cantidad de clientes FREE de este usuario"
        name="rCantidadFree"
        value={rCantidadFree}
        onChange={handleRegisterInputChange}
      />
    </div>
    
    <div className="form-group text-center mb-2">
    <label className='form-label mb-2'><b>Rol para evento</b></label>
      <Select className="form-control mb-2" options={roles} value={rol} onChange={handleRol}/>
    </div>
    {rol.value==="RRPP"&&
    <div className="form-group mb-2">
      <input
        type="number"
        title="Comision"
        className="form-control"
        placeholder="Comision que cobra el RRPP a los TARJETEROS"
        name="rComisionTarjetero"
        value={rComisionTarjetero}
        onChange={handleRegisterInputChange}
      />
    </div>}
    {rol.value!=="RRPP"&&<div className="form-group text-center">
    <label className='form-label'><b>Elegir RRPP para este TARJETERO</b></label>
      <Select className="form-control mb-2" options={rrppss} value={rrpp} onChange={handleRRPP}/>
    </div>}
        <button onClick={()=>handleAsociar()} className="btn btn-outline-primary btn-block">
          <i className="fas fa-link"></i>
          <span> Asociar</span>
        </button>
      </Modal>
    </div>
  );
};
