import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useSelector } from "react-redux";
import { AdminScreen } from "../auth/AdminScreen";
import { ClienteScreen } from "../clientes/ClienteScreen";

export const HomeScreen = () => {
  const { rol } = useSelector((state) => state.auth);
  const [isAdmin, setisAdmin] = useState(false);
  const [isRRPP, setisRRPP] = useState(false);
  const [isTarjetero, setisTarjetero] = useState(false);
  useEffect(() => {
    if(rol === 'ADMIN'){
      setisAdmin(true);
    }
    if(rol === 'RRPP'){
      setisRRPP(true);
    }
    if(rol === 'TARJETERO'|| rol === 'ESPECIAL'){
      setisTarjetero(true);
    }
  }, [setisAdmin, setisRRPP, setisTarjetero,rol]);
  return (
    <div className="container">
      {isAdmin && <AdminScreen />}
      {isRRPP && <ClienteScreen />}
      {isTarjetero && <ClienteScreen />}
    </div>
  );
};
