import React from 'react';
import { Page, Text, View, Document,StyleSheet} from '@react-pdf/renderer';

const styles = StyleSheet.create({
  table: {
    width: '100%',
    display: 'flex',
    justifyContent:'center',
    alignItems:'center',

  },
  row: {
    display: 'flex',
    flexDirection: 'row',
    borderTop: '1px solid #EEE',
    paddingTop: 8,
    paddingBottom: 8
  },
  rowRRPP: {
    display: 'flex',
    flexDirection: 'row',
    border: '2px solid #676767',
    paddingTop: 8,
    paddingBottom: 8
  },
  header: {
    borderTop: 'none',
  },
  bold: {
    fontWeight: 'bold',
  },
})

export const ComprobanteDetalleRRPP = ({datos,rrpps}) => {  
    return (
      <Document>
        <Page
          size="A4"
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "start",
            alignItems: "center",
            backgroundColor: "white", 
            padding: 25
          }}
        >
          <View
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "start",
              alignItems: "center",
              backgroundColor: "white",
              padding: 5,
            }}
          > 
            {/* <Text style={{ marginTop:"35px",fontSize: "30px" }}>Evento</Text>
            <Text style={{ color: "#08298A",fontSize: "30px",marginBottom:"10px" }}>{datos.evento ? datos.evento : ""}</Text> */}
            <Text style={{ fontSize: "20px" }}>Fecha</Text>
            <Text style={{ color: "#08298A",fontSize: "20px",marginBottom:"5px" }}>{datos.fecha ? datos.fecha : ""}</Text>
            <Text style={{ fontSize: "20px", marginBottom:"5px" }}>Planilla</Text>
            <Text style={{ fontSize: "15px" }}>Cantidad de RRPPs</Text>
            <Text style={{ color: "#08298A",fontSize: "15px",marginBottom:"10px" }}>{datos.cantRrpp ? datos.cantRrpp : ""}</Text>
            <View style={styles.table}>
              <View style={[styles.row, styles.bold, styles.header]} fixed>
                <Text style={{width: '50%', border:"1px solid black"}}>DNI</Text>
                <Text style={{width: '50%', border:"1px solid black"}}>NOMBRE</Text>
              </View>
              {rrpps.map(rrpp => (
                <>
                <View key={rrpp.dni} style={rrpp.rol==="RRPP"?styles.rowRRPP:styles.row} wrap={false}>
                  <Text style={{width: '50%'}} >{rrpp.dni}</Text>
                  <Text style={{width: '50%'}} >{rrpp.nombre} {rrpp.apellido}</Text>
                  </View>
                  <View style={styles.table}>
                    <View style={[styles.row, styles.bold, styles.header]} fixed>
                      <Text style={{width: '15%',fontSize: "10px", border:"1px solid black"}}>EVENTO</Text>
                      <Text style={{width: '14%',fontSize: "10px", border:"1px solid black"}}>ROL</Text>
                      <Text style={{width: '14%',fontSize: "10px", border:"1px solid black"}}>USUARIO</Text>
                      <Text style={{width: '14%',fontSize: "10px", border:"1px solid black"}}>DEUDA</Text>
                      <Text style={{width: '14%',fontSize: "10px", border:"1px solid black"}}>DEUDA COMISION</Text>
                      <Text style={{width: '14%',fontSize: "10px", border:"1px solid black"}}>TOTAL ENTRADAS</Text>
                      <Text style={{width: '14%',fontSize: "10px", border:"1px solid black"}}>GANANCIA ADMIN</Text>
                    </View>
                    {rrpp.eventos.map(evento => (
                      <View key={evento.evento} style={styles.row} wrap={false}>
                        <Text style={{width: '15%',fontSize: "10px"}} >{evento.eventoNombre}</Text>
                        <Text style={{width: '14%',fontSize: "10px"}} >{evento.rolEvento}</Text>
                        <Text style={{width: '14%',fontSize: "10px"}} >{evento.rrppNombre?evento.rrppNombre:"-"}</Text>
                        <Text style={{width: '14%',fontSize: "10px"}} >{evento.deuda}</Text>
                        <Text style={{width: '14%',fontSize: "10px"}} >{evento.deudaComision}</Text>
                        <Text style={{width: '14%',fontSize: "10px"}} >{evento.totalEntradas}</Text>
                        {
                          evento.rolEvento==="RRPP"
                          ?
                          <Text style={{width: '14%',fontSize: "10px"}} >{evento.totalEntradas&&evento.deuda||evento.deudaComision?evento.totalEntradas-evento.deuda:"-"}</Text>
                          :
                          <Text style={{width: '14%',fontSize: "10px"}} >{evento.totalEntradas&&evento.deuda?evento.totalEntradas-evento.deuda:"-"}</Text>
                        }
                      </View>
                    ))}
                  </View>
                  {/* {rrpp.eventos.map(evento => (
                    <Text key={evento._id} style={{width: '15%',fontSize: "15px",flexWrap:"wrap"}}>{evento.deuda}</Text>
                  ))}
                  {rrpp.eventos.map(evento => (
                    <Text key={evento._id} style={{width: '20%',fontSize: "15px",flexWrap:"wrap"}}>{evento.totalEntradas}</Text>
                  ))}
                  {rrpp.eventos.map(evento => (
                    <Text key={evento._id} style={{width: '15%',fontSize: "15px",flexWrap:"wrap"}}>{evento.totalEntradas-evento.deuda}</Text>
                  ))} */}
                </>
              ))}
            </View>
            <View style={styles.table}>
              <View style={styles.row} wrap={false}>
                  <Text style={{width: '20%'}} >TOTALES</Text>
                  <Text style={{width: '30%'}} >-</Text>
                  <Text style={{width: '15%',fontSize: "15px",flexWrap:"wrap"}}>{datos.totalDeuda}</Text>
                  <Text style={{width: '20%',fontSize: "15px",flexWrap:"wrap"}}>{datos.totalEntradas}</Text>
                  <Text style={{width: '15%',fontSize: "15px",flexWrap:"wrap"}}>{datos.totalFavor}</Text>
              </View>
            </View>
          </View>
        </Page>
      </Document>
    );
};
