import React, { useState } from 'react';
import QRCode from "qrcode.react";
import { useEffect } from 'react';
import { fetchConToken } from '../../helpers/fetch';
import { useDispatch } from 'react-redux';
import { setHasheo } from '../../actions/clientes';
import { stopWaitQr, waitQr } from '../../actions/ui';

export const QRGenerator = ({uid,dni,celular}) => {
  const [hashed, setHashed] = useState("");
  const dispatch = useDispatch();  
  useEffect(() => {
    const hashIt = async()=>{
      dispatch(waitQr());
      const resp = await fetchConToken(`clientes/sethash`,{uid,dni,celular},'POST');
      const body = await resp.json();
      setHashed(dni+celular+body.hash);
      setTimeout(()=>{
        dispatch(stopWaitQr());
      },2000);
     
    }
    hashIt();
  }, [uid,dni,celular,dispatch]);
  return (
  <div>
      <QRCode className='d-none' value={`${hashed}`} size={128} level="H" />
  </div>
  );
};
