import React, { useState } from 'react';
// import { useDispatch } from 'react-redux';
// import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
// import moment from "moment";
import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import {  fetchSinToken } from '../../helpers/fetch';
import Swal from 'sweetalert2';

export const ClienteDescargaPDF = () => {
  const location = useLocation();
  const id = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const cortado = location.pathname.split('/');
  const idEvento = cortado[2];
  const [cliente, setCliente] = useState("");
  const [url, setURL] = useState("");
  // const history = useHistory();
  // const dispatch = useDispatch();
  useEffect(() => {
    const getCliente = async() =>{
      const resp = await fetchSinToken(`clientes/clientepapdf/${idEvento}/${id}`);
      const body = await resp.json();
      if(body.status==="error"){
        Swal.fire('Error',body.message,'error');
      }else{
        setCliente(body.cliente);
        const splitComienzo = body.url.split('/',6);
        const splitEntero = body.url.split('/');
        const urll = splitComienzo[0]+"/"+splitComienzo[1]+"/"+splitComienzo[2]+"/"+splitComienzo[3]+"/"+splitComienzo[4]+"/"+splitComienzo[5]+"/fl_attachment/"+splitEntero[6]+"/"+splitEntero[7];
        setURL(urll);
      }
    }
    getCliente();
  }, [id,idEvento])
  
  return ( 
  <div className="d-flex justify-content-center align-items-center flex-column">
    <div className="col-md-6 login-form-2 mb-4 mt-5">
      <h4 style={{"textAlign":"center","color":"white"}}>DESCARGAR PDF DEL EVENTO</h4>
      <hr style={{color:"white"}}/>
      {cliente&&
      <div className="form-group d-flex justify-content-center">
        <a className='btn btn-lg btn-success' style={{"textAlign":"center","color":"white"}} href={url} target="_blank" download={"comprobante-"+cliente.dni} rel="noreferrer">Descargar PDF</a>
      </div>
      }
    </div>
  </div>
);
};
