import Swal from "sweetalert2";
import { fetchConToken, fetchSinToken } from "../helpers/fetch";
import { types } from "../types/types";

export const startLogin = (emaill, password) => {
  return async (dispatch) => {
    let email = emaill.toLowerCase();
    const resp = await fetchSinToken("auth", { email, password }, "POST");
    const body = await resp.json();
    if (body.ok) {
      localStorage.setItem("token", body.token);
      localStorage.setItem("token-init-date", new Date().getTime());
      dispatch(login({ uid: body.uid, nombre: body.nombre, rol: body.rol, maxFree: body.maxFree }));
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};
export const startRegister = (dni,email, password, nombree, apellidoo, comisionEnt,roll,comisionRRPP,rrppp,maxFree,eventoo) => {
  return async (dispatch) => {
    let rol;
    if(roll.value==="ESPECIAL"){
      rol = "ESPECIAL";
    }else{
      rol = "RRPP";
    }
    const estado = true;
    const rrpp = rrppp.value;
    const rrppNombre = rrppp.label;
    const evento = eventoo.value;
    const eventoNombre = eventoo.label;
    const rolEvento = roll.value;
    const passwordShow = password;
    let apellido = apellidoo.toUpperCase();
    let nombre = nombree.toUpperCase();
    //const evento = eventoo.value;
    const resp = await fetchConToken(
      "auth/new",
      { email, password,passwordShow, nombre, apellido, rol, dni ,estado, comisionEnt, comisionRRPP,rrpp,maxFree,evento,eventoNombre,rolEvento,rrppNombre},
      "POST"
    );
    const body = await resp.json();
    if (body.ok) {
      // dispatch(Asociar(body.uid, evento));
      Swal.fire("Exito", "RRPP Cargado con exito", "success");
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

export const startSetRrppActive = (id) => {
  return async (dispatch) => {
    const resp = await fetchConToken(`auth/edit/${id}`);
    const body = await resp.json();
    dispatch(startAsociar(body.usuario));
  };
};

export const startUpdateRrpp = (dni, email, nombree, passwordShow, apellidoo) => {
  return async (dispatch) => {
    let apellido = apellidoo.toUpperCase();
    let nombre = nombree.toUpperCase();
    const resp = await fetchConToken(
      `auth/editt`,
      {dni, email, nombre, passwordShow, apellido},
      "PUT"
    );
    const body = await resp.json();
    if (body.ok) {
      Swal.fire("Exito", "Actualizado con éxito", "success");
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

