export const Asociar = (uid,eventoo,roll,rrppp,comisionEnt,comisionRRPP,maxFree) => {
  return async (dispatch) => {
    const rrpp = rrppp.value;
    const rrppNombre = rrppp.label;
    const eventoNombre = eventoo.label;
    const rolEvento = roll.value;
    const evento = eventoo.value;
    const resp = await fetchConToken(
      'auth/asociar',
      { uid, evento,rolEvento,eventoNombre,rrppNombre,rrpp,comisionEnt,comisionRRPP,maxFree},
      "POST"
    );
    const body = await resp.json();
    if (resp.status === 201) {
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

export const startAsociar = (uid) => ({
  type: types.userAsociar,
  payload: uid,
});
export const startClearUserAsoc = () => ({
  type: types.startClearUserAsoc,
  payload: null,
});

export const startChecking = () => {
  return async (dispatch) => {
    const resp = await fetchConToken("auth/renew");
    const body = await resp.json();
    if (body.ok) {
      localStorage.setItem("token", body.token);
      localStorage.setItem("token-init-date", new Date().getTime());
      dispatch(login({ uid: body.uid, nombre: body.nombre, rol: body.rol }));
    } else {
      //Swal.fire("Error", body.msg, "error");
      dispatch(checkingFinish());
    }
  };
};
const checkingFinish = () => ({
  type: types.authCheckingFinish,
});

const login = (user) => ({
  type: types.authLogin,
  payload: user,
});

export const startLogout=()=>{
  return(dispatch)=>{
    localStorage.clear();
    dispatch(logout());
  }
}
const logout = ()=>({
  type:types.authLogout
})
