import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { borrarBody } from '../../actions/clientes';
import { useForm } from '../../hooks/useForm';
import moment from "moment";
import { useEffect } from 'react';
import Swal from 'sweetalert2';
import { useLocation } from 'react-router-dom';
import { startLogout } from '../../actions/auth';
import { fetchConToken, fetchSinToken } from '../../helpers/fetch';
import { QRGeneratorSinToken } from '../ui/QRGeneratorSinToken';
import { useFormAutoSinToken } from '../../hooks/useFormAutoSinToken';

const now = moment();

export const ClienteFreeCarga = () => {
  const location = useLocation();
  const idCliente = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const qrCodeCanvas = document.querySelector('canvas');
  const dataUrl = qrCodeCanvas&&qrCodeCanvas.toDataURL('image/jpg', 0.3);
  const [disableInput, setDisableInput] = useState(false);
  const [actualizar, setActualizar] = useState(false);
  const [uid, setUid] = useState("");
  const [evento, setEvento] = useState("");
  const [eventoid, setEventoid] = useState("");
  const [tipo, setTipo] = useState("")
  const { body, cargando } = useSelector((state) => state.clientes);
  const history = useHistory();
  const dispatch = useDispatch();
  const [formClienteValues, handleClienteInputChange,reset,handleInput] = useForm({
      cNombre:"",
      cApellido:""
  });
  const [formAutoValues, handleAutoInputChange] = useFormAutoSinToken({
    cDni:"",
  });
  const [formCelularValues, handleCelularInputChange,reset1,handleInput1,handleInput2] = useForm({
    cCelular: "",
  });
  const {cCelular} = formCelularValues;
  const { cNombre, cApellido } = formClienteValues;
  const { cDni } = formAutoValues;
  useEffect(() => {
    if(body){
      if(body.status==="error"){
        // reset();
        // reset1();
        dispatch(borrarBody());
        setDisableInput(false);
      }else{
        const nombre = document.getElementById('cNombre');
        const apellido = document.getElementById('cApellido');
        const celular = document.getElementById('cCelular');
        handleInput(apellido,body.cliente.apellido,nombre,body.cliente.nombre,celular,body.cliente.celular);
        handleInput2(celular,body.cliente.celular);
        setDisableInput(true);
        setActualizar(true);
        dispatch(borrarBody());
      }
    }
  }, [dispatch,body,handleInput,handleInput2]);
  useEffect(() => {
    const getCliente = async()=>{
      const resp = await fetchSinToken(`clientes/edit/${idCliente}`);
      const body = await resp.json();
      setUid(body.cliente.uid);
      setEvento(body.cliente.evento);
      setTipo(body.cliente.tipo);
      setEventoid(body.cliente.entradas[body.cliente.entradas.length-1].evento);
    }
    getCliente();
  }, [idCliente])
  
  const handleCargar = (e) => {
      e.preventDefault();
      if (cCelular === "" || cDni === "" || cApellido === "" || cNombre === "") {
        return Swal.fire(
          "Error",
          "Debes de rellenar todos los campos",
          "error"
        );
      }
      if(actualizar){
        const autoActualizarFree = async(id,dni,celular,fecha,dataUrl)=>{
          const respo = await fetchConToken(
            "clientes/autoupdatefree",
            { id,dni,celular,fecha,dataUrl },
            "PUT"
          );
          const bodo = await respo.json();
          if (bodo.ok) {
            Swal.fire("Exito", "Cliente nuevo cargado con éxito", "success");
            const fileUploaddd = async(dni,evento)=>{
              const resp = await fetchSinToken(
                "clientes/fileuploadsintoken",
                { dni,evento },
                "POST"
              );
              const body = await resp.json();
              if(body.ok){
                Swal.fire({title:"Exito",text:body.msg, icon:"success",showConfirmButton:false});
                setTimeout(()=>{
                  window.open(`${body.pdfurl}`)
                  dispatch(startLogout());
                  history.push('/logincliente');
                },1500);
              }
            }
            setTimeout(()=>{fileUploaddd(cDni,eventoid)},1000);
          }else{
            Swal.fire("Error", bodo.msg, "error");
            dispatch(startLogout());
            history.push('/logincliente');
          }
        }
        autoActualizarFree(idCliente,cDni, cCelular, now.toDate(),dataUrl);
      }else{
        const autoCargarFree = async(id,dni,nombree,apellidoo, celular, fecha,dataUrl)=>{
          const nombre = nombree.toUpperCase();
          // console.log(nombre)
          const apellido = apellidoo.toUpperCase();
          // console.log(apellido)
          const respp = await fetchConToken(
            "clientes/autocargafree",
            { id,dni,nombre,apellido, celular, fecha,dataUrl },
            "PUT"
          );
          const bodo = await respp.json();
          if (bodo.ok) {
            Swal.fire("Exito", "Cliente nuevo cargado con éxito", "success");
            const fileUploaddd = async(dni,evento)=>{
              const resp = await fetchSinToken(
                "clientes/fileuploadsintoken",
                { dni,evento },
                "POST"
              );
              const body = await resp.json();
              if(body.ok){
                Swal.fire({title:"Exito",text:body.msg, icon:"success",showConfirmButton:false});
                setTimeout(()=>{
                  window.open(`${body.pdfurl}`)
                  dispatch(startLogout());
                  history.push('/logincliente');
                },1500);
              }
            }
            setTimeout(()=>{fileUploaddd(dni,eventoid)},1000);
          }else{
            Swal.fire("Error", bodo.msg, "error");
            dispatch(startLogout());
            history.push('/logincliente');
          }
        }
        autoCargarFree(idCliente,cDni,cNombre,cApellido, cCelular, now.toDate(),dataUrl);
      }
  };
  const [symbolsArr] = useState(["e", "E", "+", "-", ".",","]);
  return ( 
    <div className="d-flex justify-content-center align-items-center flex-column">
  <div className="col-md-6 login-form-2 mb-4 mt-1">
  
  {/* <h3 style={{"text-align":"center","color":"white"}}>INGRESO SOLAMENTE PARA MAYORES DE 18 AÑOS</h3> */}
  <h4 style={{"text-align":"center","color":"white"}}>Ingreso al evento con DNI real en mano</h4>
  <p style={{"text-align":"center","color":"white"}}>Como ultima instancia llevar QR generado de esta carga</p>
  <p style={{"text-align":"center","color":"white"}}>Guardar comprobante QR en el celular</p>
  <hr style={{color:"white"}}/>
  <form onSubmit={handleCargar}>
  <p style={{"text-align":"center","color":"white"}}>INGRESE SUS DATOS PARA CARGARSE EN EL SISTEMA</p>

    <div className="form-group mb-5">
      <input
        type="number"
        maxLength={8}
        minLength={7}
        title="Debe poner 7 digitos como minimo o 8 digitos como maximo"
        className="form-control mb-2 border border-primary text-center"
        placeholder="DNI"
        name="cDni"
        value={cDni}
        autoComplete="off"
        onChange={(e)=>handleAutoInputChange(e)}
        onKeyDown={e => symbolsArr.includes(e.key) && e.preventDefault()}
      />
      
    </div>
    <div className="form-group">
      <input
        type="number"
        className="form-control mb-2 text-center"
        placeholder={cargando?"Cargando...":"Celular"}
        name="cCelular"
        id="cCelular"
        value={cCelular}
        onChange={handleCelularInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className={disableInput ? "form-control mb-2 border border-primary text-center text-muted" :"form-control mb-2 border border-primary text-center"}
        placeholder={cargando?"Cargando...":"Nombre"}
        name="cNombre"
        id="cNombre"
        value={cNombre}
        disabled = {disableInput}
        onChange={handleClienteInputChange}
      />
    </div>

    <div className="form-group">
      <input
        type="text"
        className={disableInput ? "form-control mb-2 border border-primary text-center text-muted" :"form-control mb-2 border border-primary text-center"}
        placeholder={cargando?"Cargando...":"Apellido"}
        name="cApellido"
        id="cApellido"
        value={cApellido}
        disabled = {disableInput}
        onChange={handleClienteInputChange}
      />
    </div>
    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Cargar" />
    </div>
  </form>
  
</div>
<QRGeneratorSinToken uid={uid} dni={cDni} celular={cCelular}/>
</div>
);
};
