import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { startDesasociarClienteSinEvento, startSetActive } from '../../actions/clientes';
import {isMobile} from 'react-device-detect';
import { useHistory } from 'react-router-dom';
import Swal from 'sweetalert2';
import { fetchConToken } from '../../helpers/fetch';
import { uiOpenModalll, uiOpenModallll } from '../../actions/ui';

export const ClienteItems = ({clientes,loading}) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const { uid } = useSelector((state) => state.auth);
  // const { activeEvent } = useSelector((state) => state.eventos);
    if(loading){
        return <h2>Cargando...</h2>
    }
    const handleGoDesasociar = (cid,evento,longitud) => {
      if(longitud>1){
        dispatch(uiOpenModalll())
        dispatch(startSetActive(cid));
      }else{
        dispatch(startDesasociarClienteSinEvento(cid,uid,evento));
        setTimeout(()=>{
          history.push(`/`);
        },500);
      }
  };
  const handleReenviarWsp= (cid,evento,longitud) => {
    if(longitud>1){
      dispatch(uiOpenModallll())
      dispatch(startSetActive(cid));
    }else{
      const reenviar = async()=>{
        const resp = await fetchConToken(`clientes/reenviarwsp/${cid}/${evento}`);
        const body = await resp.json();
        if(body.ok){
          Swal.fire({title:"PDF a WSP",text:"¿Seguro que desea reenviar el PDF por WSP?", icon:"question",confirmButtonText: 'OK. Abrir WSP WEB'}).then((result)=>{
            if(result.isConfirmed){
              window.open(`https://wa.me/549${body.celular}?text=https://event-a.herokuapp.com/descargapdf/${evento}/${cid}`);
            }
          });
        }
      }
      reenviar();
    }    
};
    const handleGoEditar = (uid) => {
      dispatch(startSetActive(uid));
      setTimeout(()=>{
        history.push(`/editarcliente/${uid}`);
      },4000);
    };
  return (
    <>
      {clientes && <table className={isMobile ? "d-block overflow-scroll table table-bordered table-striped":"table table-bordered table-striped"}>
    <thead className="bg-dark text-light">
      <tr>
        <th className="text-center align-middle small" scope="col">DNI</th>
        <th className="text-center align-middle small" scope="col">Nombre</th>
        <th className="text-center align-middle small" scope="col">Apellido</th>
        <th className="text-center align-middle small" scope="col">Celular</th>
        <th className="text-center align-middle small" scope="col">Eventos</th>
        <th className="text-center align-middle small" scope="col">Usuario</th>
        <th className="text-center align-middle small" scope="col">Reenviar PDF</th>
        <th className="text-center align-middle small" scope="col">Editar</th>
        <th className="text-center align-middle small" scope="col">Eliminar</th>
      </tr>
    </thead>
    <tbody>
    {clientes.map(item => {
        return (
          <tr key={item.uid}>
            <td className="text-center small">{ item.dni }</td>
            <td className="text-center small">{ item.nombre }</td>
            <td className="text-center small">{ item.apellido }</td>
            <td className="text-center small">{ item.celular }</td>
            {/* <td className="text-center"> */}
            {/* {item.entradas.map(entrada => {
                return entrada.usuario === uid && <pre key={entrada.evento}>{entrada.nombre} - ${entrada.importe} - {entrada.tipo}</pre>
            })} */}
            {/* </td> */}
            <td className="text-center">
            {item.entradas.map(entrada => {
              if((entrada.rol==="TARJETERO" || entrada.rol==="ESPECIAL") && entrada.usuarioRRPP===uid){
                return  <pre key={entrada.evento}>{entrada.nombre} - ${entrada.importe} - {entrada.tipo}</pre>
              }else{
                if(entrada.usuario===uid){
                  return  <pre key={entrada.evento}>{entrada.nombre} - ${entrada.importe} - {entrada.tipo}</pre>
                }
              }
            })}
            </td>
            {/* entrada.usuario === uid && */}
            {/* <td className="text-center">
            {item.entradas.map(entrada => {
                return entrada.usuario===uid &&<pre key={entrada.evento}>{entrada.nombre} - ${entrada.importe} - {entrada.tipo}</pre>
            })}
            </td> */}
            {/* usuario.usuario ===uid && */}
            <td className="text-center">
            {item.usuarios.map(usuario => {
              if((usuario.rol==="TARJETERO"  || usuario.rol==="ESPECIAL") && usuario.usuarioRRPP===uid){
                return  <pre key={usuario.usuario}>{usuario.nombre} - {usuario.apellido} </pre>
              }else{
                if(usuario.usuario===uid){
                  return  <pre key={usuario.usuario}>{usuario.nombre} - {usuario.apellido} </pre>
                }
              }
            })}
            </td>
            {/* <td className="text-center">
            {item.usuarios.map(usuario => {
                return usuario.usuario===uid &&<pre key={usuario.usuario}>{usuario.nombre} - {usuario.apellido} </pre>
            })}
            </td> */}
            {/* <td className="text-center small">{ item.usuario.nombre+" "+item.usuario.apellido }</td> */}
            {/* <td className="text-center"> */}
            {item.usuarios.map((usuario,i) => {
                if(usuario.usuario === uid){
                  return <td className="text-center small"><button className='btn btn-success' onClick={()=>handleReenviarWsp(item.uid,usuario.evento,item.usuarios.length)}><i className="fa-brands fa-whatsapp"></i></button></td>
                }
            })}
            {/* </td> */}
            {/* <td className="text-center"> */}
            {item.usuarios.map((usuario,i) => {
              if(usuario.usuario === uid){
                return <td className="text-center small"><button className='btn btn-warning' onClick={()=>handleGoEditar(item.uid)}>Editar</button></td>
              }
            })}
            {/* </td> */}
            {/* <td className="text-center"> */}
            {item.usuarios.map((usuario,i) => {
              if(usuario.usuario === uid){
                return <td className="text-center small"><button className='btn btn-danger' onClick={()=>handleGoDesasociar(item.uid,usuario.evento,item.usuarios.length)}><i className="fas fa-trash-alt"></i></button></td>
              }
            })}
            {/* </td> */}
            {/* {item.usuario._id === uid ? <td className="text-center small"><button className='btn btn-success' onClick={()=>handleReenviarWsp(item.uid)}><i className="fa-brands fa-whatsapp"></i></button></td> : <td className="text-center">-</td>} */}
            {/* {item.usuario._id === uid ? <td className="text-center small"><button className='btn btn-warning' onClick={()=>handleGoEditar(item.uid)}>Editar</button></td> : <td className="text-center">-</td>} */}
            {/* {item.usuario._id === uid ? <td className="text-center small"><button className='btn btn-danger' onClick={()=>handleGoDesasociar(item.uid)}><i className="fas fa-trash-alt"></i></button></td> : <td className="text-center">-</td>} */}
          </tr>
        );
      })}
    </tbody>
  </table>}
  </> );
};
