import { types } from "../types/types";

const initialState = {
  modalOpen: false,
  modalOpenn: false,
  modalOpennn: false,
  modalOpennnn: false,
  modalOpennnnn: false,
  waitQR:false,
  estadoWsp:"Desconectado"
};

export const uiReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.uiOpenModal:
      return {
        ...state,
        modalOpen: true,
      };
      case types.waitQr:
      return {
        ...state,
        waitQR: true,
      };
      case types.stopWaitQr:
      return {
        ...state,
        waitQR: false,
      };
    case types.uiOpenModall:
      return {
        ...state,
        modalOpenn: true,
      };
      case types.uiOpenModalll:
      return {
        ...state,
        modalOpennn: true,
      };
      case types.uiOpenModallll:
      return {
        ...state,
        modalOpennnn: true,
      };
      case types.uiOpenModalllll:
      return {
        ...state,
        modalOpennnnn: true,
      };
    case types.uiCloseModal:
      return {
        ...state,
        modalOpen: false,
      };
    case types.uiCloseModall:
      return {
        ...state,
        modalOpenn: false,
      };
      case types.uiCloseModalll:
      return {
        ...state,
        modalOpennn: false,
      };
      case types.uiCloseModallll:
      return {
        ...state,
        modalOpennnn: false,
      };
      case types.uiCloseModalllll:
      return {
        ...state,
        modalOpennnnn: false,
      };
      case types.getEstadoWsp:
        return {
          ...state,
          estadoWsp:action.payload,
        }; 
    default:
      return state;
  }
};
