import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import Swal from 'sweetalert2';
import { startUpdateRrpp } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';
import "./../auth/login.css";
import { useSelector } from 'react-redux';

export const RrppEditar = () => {
  const { userAsoc } = useSelector((state) => state.auth);
  const [formRrppValues, handleRrppInputChange] = useForm({
      rNombre:userAsoc ? userAsoc.nombre: "",
      rApellido:userAsoc ? userAsoc.apellido: "",
      rEmail: userAsoc ? userAsoc.email:"",
      rPassword:userAsoc ? userAsoc.passwordShow: "",
      rComision:userAsoc ? userAsoc.comisionEnt:"",
      rComisionTarjetero:userAsoc ? userAsoc.comisionRRPP:"",
      rCantidadFree:userAsoc ? userAsoc.maxFree:""
  });
  const dispatch = useDispatch();
  const history = useHistory();
  const [isOculto, setIsOculto] = useState(true);
  const { rNombre, rApellido, rPassword, rEmail } = formRrppValues;

  const handleUpdate = (e) => {
        e.preventDefault();
        if ( rNombre === "" || rApellido === "" || rPassword === "" || rEmail === "") {
          return Swal.fire(
            "Error",
            "Debes de rellenar todos los campos",
            "error"
          );
        }
        dispatch(startUpdateRrpp( userAsoc.dni, rEmail, rNombre, rPassword, rApellido));
        setTimeout(()=>{
          history.goBack();
        },500);
  };
  const handleVolver = (e) => {
        history.push('/controlrpp');
  };
  const setOculto = () => {
    if(isOculto){
      setIsOculto(false);
    }else{
      setIsOculto(true);
    }
};
  return ( 
  <div className="col-md-6 login-form-2">
  <h3> Actualizar RPP</h3>
  {userAsoc && <h5 className="text-center" style={{color:"white"}}> DNI - {userAsoc.dni}</h5> }
  <form onSubmit={handleUpdate}>
    <div className="form-group text-center">
      <label style={{color:"white"}}>Nombre</label>
      <input
        type="text"
        className="form-control mb-2"
        placeholder="Nombre"
        name="rNombre"
        value={rNombre}
        onChange={handleRrppInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label style={{color:"white"}}>Apellido</label>
      <input
        type="text"
        className="form-control mb-2"
        placeholder="Apellido"
        name="rApellido"
        value={rApellido}
        onChange={handleRrppInputChange}
      />
    </div>
    <div className="form-group text-center">
    <label style={{color:"white"}}>Email</label>
      <input
        type="email"
        className="form-control mb-2"
        placeholder="Correo"
        name="rEmail"
        value={rEmail}
        autoComplete="new-password"
        onChange={handleRrppInputChange}
      />
    </div>
    <div className="form-group text-center mb-4">
    <label style={{color:"white"}}>Contraseña</label>
      <div className='form-control'>
      <input
        type={isOculto?"password":"text"}
        className="w-75 mb-2"
        placeholder="Contraseña"
        name="rPassword"
        value={rPassword}
        autoComplete="new-password"
        onChange={handleRrppInputChange}
      />
      <button className={isOculto?'btn btn-success w-25':'btn btn-danger w-25'} onClick={setOculto} type="button">{isOculto?"Mostrar":"Ocultar"}</button>
      </div>
    </div>

    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Actualizar" />
    </div>
  </form>
  <div className="form-group d-flex justify-content-center">
    <button className="btnSubmit mt-2" onClick={handleVolver}>Volver</button>
  </div>
</div>);
};
