import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { fetchConToken } from '../../helpers/fetch';
import { ClienteItems } from './ClienteItems';
import { Pagination } from '../ui/Pagination';
import { useSelector } from 'react-redux';
import { AsociarClienteModal } from './AsociarClienteModal';
import { useDispatch } from 'react-redux';
import {isMobile} from 'react-device-detect';
import { AsociarEventoModal } from './AsociarEventoModal';
import { uiOpenModall } from '../../actions/ui';
import { eventClearIsFilter } from '../../actions/eventos';
import { AsociarEventoClienteModal } from './AsociarEventoClienteModal';
import { AsociarReenviarWSPModal } from './AsociarReenviarWSPModal';
import { AsociarEventoCeluModal } from './AsociarEventoCeluModal';
import Select from 'react-select';

export const ClienteScreen = () => {
  const dispatch = useDispatch();
  const { uid, rol } = useSelector((state) => state.auth);
  const { isFilter } = useSelector((state) => state.eventos);
  const { activeEvent } = useSelector((state) => state.eventos);
  const [ganTotal, setGanTotal] = useState(0);
  const [ganComision, setGanComision] = useState(0);
  const [ganClientes, setGanClientes] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [clientes, setClientes] = useState([]);
  const [rrpps, setRRPPS] = useState([]);
  const [loading, setLoading] = useState(false);
  const [clientesPerPage, setClientesPerPage] = useState(10);
  const indexOfLastCliente = currentPage * clientesPerPage;
  const indexOfFirstCliente = indexOfLastCliente - clientesPerPage;
  const currentClientes = clientes.slice(indexOfFirstCliente,indexOfLastCliente);
  //FETCHING
  useEffect(() => {
    const getClientesByRpp = async() =>{
      setLoading(true);
      const resp = await fetchConToken(`clientes/${uid}`);
      const body = await resp.json();
      setClientes(body.clientess);
      setLoading(false);
    }
    
    const getClientesByEventoAndRRPP = async() =>{
      setLoading(true);
      const resp = await fetchConToken(`clientes/rrpp/${uid}/evento/${activeEvent.value}`);
      const body = await resp.json();
      setClientes(body.clientes);
      setLoading(false);
    }
    // const getGananciasByOperativo = async() =>{
    //   const resp = await fetchConToken(`auth/rpps/gananciaop/${uid}`);
    //   const body = await resp.json();
    //   setGanClientes(body.ganclientes);
    // }
    const getGananciasByGeneral = async() =>{
      const resp = await fetchConToken(`auth/rpps/ganancia/${uid}`);
      const body = await resp.json();
      setGanClientes(body.ganclientes);
      setGanComision(body.gancomision);
      setGanTotal(body.gantotal);
    }
    // const getClientesFreeByRRPP = async() =>{
    //   const resp = await fetchConToken(`clientes/getfree/${uid}`);
    //   const body = await resp.json();
    // }
    if(rol==="RRPP"){
      getGananciasByGeneral();
    }
    // else{
    //   // getGananciasByOperativo();
    // }
    if(isFilter){
      getClientesByEventoAndRRPP();
    }else{
      getClientesByRpp();
    }
    // getClientesFreeByRRPP();
  }, [rol,uid,isFilter,activeEvent]);
  useEffect(() => {
    const getRRPPByEvento = async() =>{
      const resp = await fetchConToken(`auth/rrpps/byrrpp/${uid}`);
      const body = await resp.json();
      setRRPPS(body.rpps);
    }
    getRRPPByEvento();
  }, [uid]);
  // const [formSearchValues] = useForm({
  //   cSearch:""
  // });
  const [values, setValues] = useState({cSearch:""});
  const [valuesTar, setValuesTar] = useState({cSearchTar:""});
  const handleSearchInputChange = ({ target }) => {
    const getClientesByRpp = async() =>{
      setLoading(true);
      const resp = await fetchConToken(`clientes/${uid}`);
      const body = await resp.json();
      setClientes(body.clientess);
      setLoading(false);
    }
    const getGananciasByGeneral = async() =>{
      const resp = await fetchConToken(`auth/rpps/ganancia/${uid}`);
      const body = await resp.json();
      setGanClientes(body.ganclientes);
      setGanComision(body.gancomision);
      setGanTotal(body.gantotal);
    }
    if(target.value===""){
      getClientesByRpp();
      getGananciasByGeneral();
    }
    setValues({
      ...values,
      [target.name]: target.value,
    });
  };
  const handleSearchTarInputChange = ({ target }) => {
    const getClientesByRpp = async() =>{
      setLoading(true);
      const resp = await fetchConToken(`clientes/${uid}`);
      const body = await resp.json();
      setClientes(body.clientess);
      setLoading(false);
    }
    const getGananciasByGeneral = async() =>{
      const resp = await fetchConToken(`auth/rpps/ganancia/${uid}`);
      const body = await resp.json();
      setGanClientes(body.ganclientes);
      setGanComision(body.gancomision);
      setGanTotal(body.gantotal);
    }
    if(target.value===""){
      getClientesByRpp();
      getGananciasByGeneral();
    }
    setValuesTar({
      ...valuesTar,
      [target.name]: target.value,
    });
  };
  const { cSearch } = values;
  const { cSearchTar } = valuesTar;
  const handleBuscar = (e) => {
    e.preventDefault();
    const searchCliente = async() =>{
      const resp = await fetchConToken(`clientes/search/${cSearch}/${uid}`);
      const body = await resp.json();
      setClientes(body.clientess);
      setGanClientes(body.ganclientes);
      setGanComision(body.gancomision);
      setGanTotal(body.gantotal);
    }
    searchCliente();
  };
  const handleBuscarPorTarjetero = (e) => {
    e.preventDefault();
    const searchClientePorTarjetero = async() =>{
      const resp = await fetchConToken(`clientes/searchportar/${cSearchTar}/${uid}`);
      const body = await resp.json();
      setClientes(body.clientess);
      setGanClientes(body.ganclientes);
      setGanComision(body.gancomision);
      setGanTotal(body.gantotal);
    }
    searchClientePorTarjetero();
  };
  const handleGoFilter = () => {
    dispatch(uiOpenModall());
  };
  const handleGoLimpiar= () => {
    dispatch(eventClearIsFilter());
  };
  const handleFiltrarRRPP = (e) => {
    e.preventDefault();
    const filtrarRRP = async() =>{
        const resp = await fetchConToken(`clientes/${rrpp.value}`);
        const body = await resp.json();
        // console.log(body)
        setClientes(body.clientess);
    }
    filtrarRRP();
};

  let rrppss = [];
  for (let i = 0; i < rrpps.length; i++) {
  const evento = {
    value:rrpps[i].uid,
    label:rrpps[i].nombre +" - "+rrpps[i].apellido,
  }
  rrppss.push(evento);
  }
  const [rrpp, setRRPP] = useState("");
  const handleRRPP = (e) => {
      setRRPP(e);
  };

  return ( 
    <div className="container">
      {isMobile
      ?
      <div className="d-flex flex-column justify-content-center align-items-center">
      <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleBuscar}>
        <input className='form-control me-2 text-center' title='Busqueda por apellido' placeholder='Ingrese el apellido del cliente...' type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
        <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
      </form>
      <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleBuscarPorTarjetero}>
        <input className='form-control me-2 text-center' title='Busqueda por apellido' placeholder='Ingrese el apellido del tarjetero...' type="text" name="cSearchTar" value={cSearchTar} onChange={handleSearchTarInputChange}/>
        <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
      </form>
      {rol==="TARJETERO"&&<div className='d-flex justify-content-center align-items-center mb-2'>
        <div className='form-group text-center mb-2'>
          <label><b>Ganancia</b></label>
          <input disabled={true} className='form-control'  type="text" value={`$ ${ganClientes?ganClientes:0}`}/>
        </div>
      </div>}
      <div className='d-flex flex-row justify-content-center align-items-center'>
      {rol==="ESPECIAL"?<Link className=' d-flex justify-content-end text-decoration-none' to="/addclienteespecial">
        <button className="btn btn-success" title="Cargar un nuevo Cliente especial">
          <i className="fas fa-user-plus"></i> Cliente especial
        </button>
      </Link>
      :<Link className=' d-flex justify-content-end text-decoration-none' to="/addcliente">
        <button className="btn btn-success" title="Cargar un nuevo Cliente">
          <i className="fas fa-user-plus"></i> Cliente
        </button>
      </Link>}
      </div>
      </div>
      :
      <div className="d-flex flex-column justify-content-between align-items-center">
      <div className='form-group d-flex flex-row justify-content-center align-items-center mb-2 w-100'>
      <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleBuscar}>
        <input className='form-control me-2 text-center' title='Busqueda de cliente por apellido' placeholder='Ingrese el apellido...' type="text" name="cSearch" value={cSearch} onChange={handleSearchInputChange}/>
        <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
      </form>
      <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleBuscarPorTarjetero}>
        <input className='form-control me-2 text-center' title='Busqueda de cliente por apellido de Tarjetero' placeholder='Ingrese el apellido del tarjetero...' type="text" name="cSearchTar" value={cSearchTar} onChange={handleSearchTarInputChange}/>
        <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
      </form>
      </div>
      {rol==="TARJETERO"&&<div className='d-flex justify-content-center align-items-center'>
        <div className='form-group text-center mb-2'>
          <label><b>Ganancia</b></label>
          <input disabled={true} className='form-control'  type="text" value={`$ ${ganClientes?ganClientes:0}`}/>
        </div>
      </div>}
      <div className='d-flex flex-row justify-content-center align-items-center'>
      {rol==="ESPECIAL"?<Link className=' d-flex justify-content-end text-decoration-none' to="/addclienteespecial">
        <button className="btn btn-success" title="Cargar un nuevo Cliente especial">
          <i className="fas fa-user-plus"></i> Cliente especial
        </button>
      </Link>
      :<Link className=' d-flex justify-content-end text-decoration-none' to="/addcliente">
        <button className="btn btn-success" title="Cargar un nuevo Cliente">
          <i className="fas fa-user-plus"></i> Cliente
        </button>
      </Link>}
      </div>
      </div>
      }
      
      <div className="d-flex flex-column justify-content-center align-items-center">
      {rol==="RRPP"&& <div className='d-flex justify-content-center align-items-center mb-2'>
        <div className='form-group text-center me-2'>
          <label><b>Ganancia total</b></label>
          <input disabled={true} className='form-control' type="text" value={`$ ${ganTotal?ganTotal:0}`}/>
        </div>
        <div className='form-group text-center me-2'>
          <label><b>Ganancia de comision</b></label>
          <input disabled={true} className='form-control' type="text" value={`$ ${ganComision?ganComision:0}`}/>
        </div>
        <div className='form-group text-center'>
          <label><b>Ganancia de clientes propios</b></label>
          <input disabled={true} className='form-control'  type="text" value={`$ ${ganClientes?ganClientes:0}`}/>
        </div>
      </div>}
        <h4>Cantidad de Clientes</h4>
        <h3 className='border p-2 mb-3 '>{clientes.length}</h3>
        {isMobile
        ?
        <div className='d-flex w-100 flex-column'>
        <button className="btn btn-primary mb-2 me-2" onClick={handleGoFilter} title="Filtrar por Evento">
          <i className="fas fa-filter"></i> Filtrar por Evento
        </button>
        <div className='d-flex justify-content-lg-end'>
            <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleFiltrarRRPP}>
                <Select className='w-100 me-2' options={rrppss} value={rrpp} onChange={handleRRPP}/>
                <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
            </form>
        </div>
        {isFilter &&<button className="btn btn-outline-danger mb-2 ms-2" onClick={handleGoLimpiar} title="Sacar filtro">
          <i className="fas fa-close"></i> Filtrado por evento: {activeEvent.label}
        </button>}
        </div>
        :
        <div className='d-flex w-100 flex-row'>
        <div className='d-flex justify-content-lg-end w-25'></div>
        <div className='d-flex justify-content-lg-end w-25'></div>
        <div className='d-flex justify-content-lg-end w-25'></div>
        <button className="btn btn-primary mb-2 me-2 w-75" onClick={handleGoFilter} title="Filtrar por Evento">
          <i className="fas fa-filter"></i> Filtrar por Evento
        </button>
        <div className='d-flex justify-content-lg-end w-75'>
            <form className='form-group d-flex justify-content-center align-items-center mb-2 w-100' onSubmit={handleFiltrarRRPP}>
                <Select className='w-100 me-2' options={rrppss} value={rrpp} onChange={handleRRPP}/>
                <button type="submit" className='btn btn-primary'><i className="fas fa-search"></i></button>
            </form>
        </div>
        {isFilter &&<button className="btn btn-outline-danger mb-2 ms-2" onClick={handleGoLimpiar} title="Sacar filtro">
          <i className="fas fa-close"></i> Filtrado por evento: {activeEvent.label}
        </button>}
        </div>
        }
        <ClienteItems clientes={currentClientes} loading={loading}/>
        <AsociarClienteModal />
        <AsociarEventoModal />
        <AsociarEventoClienteModal />
        <AsociarReenviarWSPModal />
        <Pagination dataPerPage={clientesPerPage} totalData={clientes.length} data={clientes} setCurrentPage={setCurrentPage}/>
      </div>
    </div>
  );
};
