export const startVerificarHash = (hash,evento) => {
  return async (dispatch) => {
    const resp = await fetchConToken(`clientes/hash/${hash}/${evento}`);
    const body = await resp.json();
    if (resp.status === 200) {
      let dni = `${body.cliente.dni}`;
      let nombre = `${body.cliente.nombre.toUpperCase()} ${body.cliente.apellido.toUpperCase()}`;
      let horarioent =`${moment(body.cliente.entrada).format('HH:mm')}`;
      Swal.fire({title:"INGRESO VERIFICADO", html:'<pre> <b>DNI</b> <br/>' + dni +'<br/> <b>Nombre completo</b> <br/>'+ nombre +'<br/> <b>Horario de entrada</b> <br/>'+ horarioent + '</pre>', icon:"success"}).then((result)=>{
        if(result.isConfirmed){
          const getClientesVerificados= async() =>{
            setLoading(true);
            const resp = await fetchConToken(`clientes/verificados/${evento}`);
            const body = await resp.json();
            setClientes(body.clientes);
            setLoading(false);
          }
          setTimeout(() => {
            getClientesVerificados();
          }, 500);
        }
      });
    } else {
      Swal.fire("Error", body.msg, "error");
    }
  };
};

