export const startUpdateCliente = (dni,celular,fecha,usuario,evento,tipo,dataUrl,eventoid) => {
  return async (dispatch) => {
    const resp = await fetchConToken(
      `clientes/editt`,
      {dni,celular,fecha,usuario,evento,tipo,dataUrl,eventoid},
      "PUT"
    );
    const body = await resp.json();
    if (body.ok) {
      Swal.fire("Exito", "Actualizado con éxito", "success");
      const fileUploadd = async(dni,evento)=>{
        const resp = await fetchConToken(
          "clientes/fileupload",
          { dni,evento },
          "POST"
        );
        const body = await resp.json();
        if(body.ok){
          Swal.fire({title:"Exito",text:"PDF subido", icon:"success",showConfirmButton:false});
        }
      }
      setTimeout(()=>{fileUploadd(dni,eventoid)},500);
    } else {
      Swal.fire("Error", body.msg, "error");
    }
    
  };
};

