export const startVerificarDni = (dni,evento) => {
    return async (dispatch) => {
      const resp = await fetchConToken(`clientes/dnisinupdate/${dni}/${evento}`);
      const bodi = await resp.json();
      if(bodi.ok){
        let dni = `${bodi.cliente.dni}`;
        let nombre = `${bodi.cliente.nombre.toUpperCase()} ${bodi.cliente.apellido.toUpperCase()}`;
        let tipo = `${bodi.cliente.tipo}`;
        Swal.fire({title:"CLIENTE", html:'<pre> <b>DNI</b> <br/>' + dni +'<br/> <b>Nombre completo</b> <br/>'+ nombre +'<br/> <b>Tipo de entrada</b> <br/>'+tipo+ '</pre>', icon:"info",showCancelButton:true,cancelButtonColor: '#d33'}).then(async(result)=>{
          if(result.isConfirmed){
            const resp = await fetchConToken(`clientes/dni/${dni}/${evento}`);
            const body = await resp.json();
            if (resp.status === 201) {
              let dni = `${body.cliente.dni}`;
              let nombre = `${body.cliente.nombre.toUpperCase()} ${body.cliente.apellido.toUpperCase()}`;
              let horarioent =`${moment(body.cliente.entrada).format('HH:mm')}`;
              let tipo = `${body.cliente.tipo}`;
              if(body.ok){
                Swal.fire({title:"INGRESO", html:'<pre> <b>DNI</b> <br/>' + dni +'<br/> <b>Nombre completo</b> <br/>'+ nombre +'<br/> <b>Horario de entrada</b> <br/>'+ horarioent + '<br/> <b>Tipo de entrada</b> <br/>'+ tipo + '</pre>', icon:"success"});
              }else{
                Swal.fire("Error", body.msg, "error");
              }
              dispatch(setLoading());
            } else {
              Swal.fire("Error", body.msg, "error");
            }
          }else{
            Swal.fire("Atencion", "Cliente no ingreso", "error");
          }
        });
      }else{
        Swal.fire("Error", bodi.msg, "error");
      }
    };
};

export const getClientes = () => {
  return async (dispatch) => {
    const resp = await fetchConToken('clientes');
    const body = await resp.json();
    dispatch(setClientes(body.clientes));
  };
};

export const startSetActive = (id) => {
  return async (dispatch) => {
    const resp = await fetchConToken(`clientes/edit/${id}`);
    const body = await resp.json();
    dispatch(startAsociarCliente(body.cliente));
  };
};


