import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useSelector, useDispatch } from "react-redux";
import { useLocation } from 'react-router-dom';
import Select from 'react-select';
import "../modal.css";
import Swal from "sweetalert2";
import { fetchConToken } from "../../helpers/fetch";
import { uiCloseModal } from "../../actions/ui";
import { useForm } from "../../hooks/useForm";


const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};
if (process.env.NODE_ENV !== "test") {
  Modal.setAppElement("#root");
}

export const EnviarMensajeCelu = () => {
  const dispatch = useDispatch();
  // const history = useHistory();
  const location = useLocation();
  const idEvento = location.pathname.substring(location.pathname.lastIndexOf('/') + 1);
  const { modalOpen,estadoWsp } = useSelector((state) => state.ui);
  const [formTextValues, handleTextInputChange] = useForm({
    cText:"",
  });
  const { cText } = formTextValues;

  const closeModal = () => {
    dispatch(uiCloseModal());
  };
  const handleCelu= () => {
    const getNumeros = async() =>{
      const respu = await fetchConToken(`wsp/chat/sendmessages/${idEvento}/${cText}`);
      const body = await respu.json();
      if(body.ok){
          Swal.fire('Exito',body.msg,'success');
      }else{
          Swal.fire('Error',body.msg,'error');
      }
    }
    getNumeros();
    closeModal();
  };
  return (
    <div className="d-flex justify-content-center align-items-center">
      <Modal
        isOpen={modalOpen}
        onRequestClose={closeModal}
        style={customStyles}
        closeTimeoutMS={200}
        className="modal"
        // overlayClassName="modal-fondo"
        ariaHideApp={process.env.NODE_ENV==="test"}
      > 
        <h1 className="black">Ingrese el texto a enviar a los celulares asociados a este evento</h1>
        <hr />
        <div className="form-control">
        <input
        type="text"
        className="form-control mb-2 border border-primary text-center"
        placeholder="Texto"
        name="cText"
        id="cText"
        value={cText}
        onChange={handleTextInputChange}
      />
        </div>
        <div className="mt-5">
        {estadoWsp === "Conectado"&&<button onClick={()=>handleCelu()} className="btn btn-outline-success btn-block">
          <i className="fas fa-message"></i>
          <span> Enviar</span>
        </button>}
        </div>
      </Modal>
    </div>
  );
};
