import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { asociarCliente, borrarBody } from '../../actions/clientes';
import { useForm } from '../../hooks/useForm';
import moment from "moment";
import Select from 'react-select';
import { useEffect } from 'react';
import { getPrecios, setEventos, setEventosByRpp } from '../../actions/eventos';
import { QRGenerator } from '../ui/QRGenerator';
import { fetchConToken } from '../../helpers/fetch';
import Swal from 'sweetalert2';
import { useFormAuto } from '../../hooks/useFormAuto';

const now = moment();

export const AddClienteEspecial = () => {
  const [disableInput, setDisableInput] = useState(false);
  const [actualizar, setActualizar] = useState(false);
  const qrCodeCanvas = document.querySelector('canvas');
  const dataUrl = qrCodeCanvas&&qrCodeCanvas.toDataURL('image/jpg', 0.3);
  const { uid } = useSelector((state) => state.auth);
  const { body } = useSelector((state) => state.clientes);
  const history = useHistory();
  const dispatch = useDispatch();
  const [formClienteValues, handleClienteInputChange,reset,handleInput] = useForm({
      cNombre:"",
      cApellido:""
  });
  const [formAutoValues, handleAutoInputChange] = useFormAuto({
    cDni:"",
  });
  const [formCelularValues, handleCelularInputChange,reset1,handleInput1,handleInput2] = useForm({
    cCelular: "",
  });
  const {cCelular} = formCelularValues;
  const { cNombre, cApellido } = formClienteValues;
  const { cDni } = formAutoValues;
  const { eventos, precios } = useSelector((state) => state.eventos);
  let eventoss = [];
  for (let i = 0; i < eventos.length; i++) {
    const evento = {
      value:eventos[i].id,
      label:eventos[i].nombre +" - "+moment(eventos[i].fecha).format('DD/MM/YYYY'),
    }
    eventoss.push(evento);
  }

  let tipos = [];
  if(precios){
    tipos = [
      { value: 0, label: `Invitado Especial`},
    ];
  }
  let tipos2 = tipos.filter(value=>{
      // if(value.value!==0){
        return value;
      // }
  });
  const [evento, setEvento] = useState("");
  const [tipo, setTipo] = useState("");
  useEffect(() => {
    dispatch(setEventosByRpp(uid));
    if(body){
      if(body.status==="error"){
        reset();
        reset1();
        dispatch(borrarBody());
        setDisableInput(false);
      }else{
        const nombre = document.getElementById('cNombre');
        const apellido = document.getElementById('cApellido');
        const celular = document.getElementById('cCelular');
        handleInput(apellido,body.cliente.apellido,nombre,body.cliente.nombre,celular,body.cliente.celular);
        handleInput2(celular,body.cliente.celular);
        setDisableInput(true);
        setActualizar(true);
        dispatch(borrarBody());
      }
    }
  }, [uid,dispatch,body,handleInput,handleInput2,reset,reset1]);
  const handleCargar = (e) => {
      e.preventDefault();
      if (cCelular === "" || cDni === "" || cApellido === "" || cNombre === "" || evento ==="" || tipo === "") {
        return Swal.fire(
          "Error",
          "Debes de rellenar todos los campos",
          "error"
        );
      }
      // if (cDni.length!==8) {
      //   return Swal.fire(
      //     "Error",
      //     "El DNI debe tener 8 digitos para ser válido",
      //     "error"
      //   );
      // }
      if(actualizar){
        const startActualizar = async(dni,celular,fecha,usuario,eventoo,tipoo,dataUrl)=>{
          const evento = eventoo.label;
          const eventoid = eventoo.value;
          const tipo = tipoo.label; 
          const free = true;
          const ticket = false;
          const resp = await fetchConToken(
            "clientes/update",
            { dni,celular,fecha,usuario,dataUrl,evento,tipo,free,eventoid,ticket },
            "POST"
          );
          const body = await resp.json();
          if (body.ok) {
            dispatch(asociarCliente(body.cliente.uid, eventoo, tipoo, usuario,celular));
            Swal.fire("Exito", "Cliente actualizado con éxito", "success");
            const fileUploaddd = async(dni,evento)=>{
              const resp = await fetchConToken(
                "clientes/fileupload",
                { dni,evento },
                "POST"
              );
              const body = await resp.json();
              if(body.ok){
                Swal.fire({title:"Exito",text:body.msg, icon:"success",confirmButtonText: 'OK. Abrir WSP WEB'}).then((result)=>{
                  if(result.isConfirmed){
                    window.open(`https://wa.me/549${cCelular}?text=Hola! aqui te dejo el link con el pdf para el ingreso al evento. Muchas gracias! https://event-a.herokuapp.com/descargapdf/${eventoid}/${body.id}`);
                    // window.open(`https://wa.me/549${cCelular}?text=${body.pdfurl}`)
                    setTimeout(()=>{window.location.reload(false);},1000);
                  }
                });
              }
            }
            setTimeout(()=>{fileUploaddd(cDni.trim(),eventoo.value)},1000);
          } else {
            if(body.oki){
              dispatch(asociarCliente(body.cliente.uid, eventoo, tipoo, usuario,celular));
              Swal.fire("Exito", "Cliente asociado con éxito", "success");
              const fileUploadd = async(dni,evento)=>{
                const resp = await fetchConToken(
                  "clientes/fileupload",
                  { dni,evento },
                  "POST"
                );
                const body = await resp.json();
                if(body.ok){
                  Swal.fire({title:"Exito",text:body.msg, icon:"success",confirmButtonText: 'OK. Abrir WSP WEB'}).then((result)=>{
                    if(result.isConfirmed){
                      window.open(`https://wa.me/549${cCelular}?text=Hola! aqui te dejo el link con el pdf para el ingreso al evento. Muchas gracias! https://event-a.herokuapp.com/descargapdf/${eventoid}/${body.id}`);
                      // window.open(`https://wa.me/549${cCelular}?text=${body.pdfurl}`)
                      setTimeout(()=>{window.location.reload(false);},1000);
                    }
                  });
                }
              }
              setTimeout(()=>{fileUploadd(cDni.trim(),eventoo.value)},1000);
            }else{
              Swal.fire("Error", body.msg, "error");
            }
          }        
        }
        startActualizar(cDni.trim(), cCelular, now.toDate(), uid,evento,tipo,dataUrl);
        setDisableInput(false);
        setActualizar(false);
      }else{
        const startCargar = async(dni,nombree,apellidoo,celular,fecha,estado,usuario,eventoo,tipoo,dataUrl)=>{
          let tipo = tipoo.label;
          let evento = eventoo.label;
          const eventoid = eventoo.value;
          const free = true;
          const ticket = false;
          const nombre = nombree.toUpperCase();
          const apellido = apellidoo.toUpperCase();
          const resp = await fetchConToken(
            "clientes",
            { dni,nombre,apellido,celular,fecha,estado,usuario,dataUrl,evento,tipo,free,eventoid,ticket },
            "POST"
          );
          const body = await resp.json();
          if (body.ok) {
            dispatch(asociarCliente(body.cliente.uid, eventoo, tipoo,usuario,celular));
            Swal.fire("Exito", "Cliente nuevo cargado con éxito", "success");
            const fileUploadd = async(dni,evento)=>{
              const resp = await fetchConToken(
                "clientes/fileupload",
                { dni,evento },
                "POST"
              );
              const body = await resp.json();
              if(body.ok){
                // Swal.fire({title:"Exito",text:body.msg, icon:"success",showConfirmButton:false});
                // setTimeout(()=>{window.open(`https://wa.me/549${cCelular}?text=${body.pdfurl}`)},1500);
                Swal.fire({title:"Exito",text:body.msg, icon:"success",confirmButtonText: 'OK. Abrir WSP WEB'}).then((result)=>{
                  if(result.isConfirmed){
                    window.open(`https://wa.me/549${cCelular}?text=Hola! aqui te dejo el link con el pdf para el ingreso al evento. Muchas gracias! https://event-a.herokuapp.com/descargapdf/${eventoid}/${body.id}`);
                    // window.open(`https://wa.me/549${cCelular}?text=${body.pdfurl}`)
                    setTimeout(()=>{window.location.reload(false);},1000);
                  }
                });
              }
            }
            setTimeout(()=>{fileUploadd(cDni.trim(),eventoo.value)},1000);
          } else {
            if(body.oki){
              dispatch(asociarCliente(body.cliente.uid, eventoo, tipoo,usuario,celular));
              Swal.fire("Exito", "Cliente asociado con éxito", "success");
              const fileUploadd = async(dni,evento)=>{
                const resp = await fetchConToken(
                  "clientes/fileupload",
                  { dni,evento },
                  "POST"
                );
                const body = await resp.json();
                if(body.ok){
                  Swal.fire({title:"Exito",text:body.msg, icon:"success",confirmButtonText: 'OK. Abrir WSP WEB'}).then((result)=>{
                    if(result.isConfirmed){
                      window.open(`https://wa.me/549${cCelular}?text=Hola! aqui te dejo el link con el pdf para el ingreso al evento. Muchas gracias! https://event-a.herokuapp.com/descargapdf/${eventoid}/${body.id}`);
                      // window.open(`https://wa.me/549${cCelular}?text=${body.pdfurl}`)
                      setTimeout(()=>{window.location.reload(false);},1000);
                    }
                  });
                }
              }
              setTimeout(()=>{fileUploadd(cDni.trim(),eventoo.value)},1000);
            }else{
            Swal.fire("Error", body.msg, "error");
            }
          }
        }
        startCargar(cDni.trim(), cNombre.trim(), cApellido.trim(), cCelular, now.toDate(), true, uid,evento,tipo,dataUrl);
        setDisableInput(false);
        setActualizar(false);
      }
  };
  const handleVolver = (e) => {
    history.push('/');
  };
  const handleEvento = (e) => {
    setEvento(e);
    dispatch(getPrecios(e.value));
  };
  const handleTipo = (e) => {
    setTipo(e);
  };
  //BLOQUEA LOS SIMBOLOS EN EL DNI
  const [symbolsArr] = useState(["e", "E", "+", "-", ".",","]);
  return ( 
  <div className="col-md-6 login-form-2 mb-4">
  <h3>Cargar cliente</h3>
  <hr style={{color:"white"}}/>
  <form onSubmit={handleCargar}>
    <div className="form-group mb-3">
    <label style={{"color":"white"}}>Ingresar DNI sin puntos ni espacios</label>
      <input
        type="number"
        maxLength={8}
        minLength={7}
        title="Debe poner 7 digitos como minimo o 8 digitos como maximo"
        className="form-control mb-2 border border-primary text-center"
        placeholder="DNI"
        name="cDni"
        value={cDni}
        autoComplete="off"
        onChange={(e)=>handleAutoInputChange(e)}
        onKeyDown={e => symbolsArr.includes(e.key) && e.preventDefault()}
      />
      
    </div>
    <div className="form-group">
    <div className='text-center'>
    <label style={{"color":"white"}}>Cod.Área+Nº Celular</label>
    <br/>
      <label style={{"color":"white"}}>Ejemplo: 3482012345</label>
      </div>
      <input
        type="number"
        className="form-control mb-2 text-center"
        placeholder="Celular"
        name="cCelular"
        id="cCelular"
        value={cCelular}
        onChange={handleCelularInputChange}
      />
    </div>
    <div className="form-group">
      <input
        type="text"
        className={disableInput ? "form-control mb-2 border border-primary text-center text-muted" :"form-control mb-2 border border-primary text-center"}
        placeholder="Nombre"
        name="cNombre"
        id="cNombre"
        value={cNombre}
        disabled = {disableInput}
        onChange={handleClienteInputChange}
      />
    </div>

    <div className="form-group">
      <input
        type="text"
        className={disableInput ? "form-control mb-2 border border-primary text-center text-muted" :"form-control mb-2 border border-primary text-center"}
        placeholder="Apellido"
        name="cApellido"
        id="cApellido"
        value={cApellido}
        disabled = {disableInput}
        onChange={handleClienteInputChange}
      />
    </div>
        <h3>Elegir evento</h3>
        <hr style={{color:"white"}}/>
        <div className='text-center'>
        <label className='form-label' style={{color:"white"}}><b>Evento</b></label>
        <div className="form-control mb-2">
           <Select options={eventoss} value={evento} onChange={handleEvento}/>
        </div>
        <label className='form-label' style={{color:"white"}}><b>Entrada</b></label>
        {precios&&<div className="form-control mb-5">
           <Select options={tipos2} value={tipo} onChange={handleTipo}/>
        </div>}
        </div>
    <div className="form-group d-flex justify-content-center">
      <input type="submit" className="btnSubmit" value="Cargar cliente" />
    </div>
  </form>
  <div className="form-group d-flex justify-content-center">
    <button className="btnSubmit mt-2" onClick={handleVolver}>Volver</button>
  </div>
  <QRGenerator uid={uid} dni={cDni} celular={cCelular}/>
</div>);
 
};
