import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import {  startUpdateCliente } from '../../actions/clientes';
import { useForm } from '../../hooks/useForm';
import { QRGenerator } from '../ui/QRGenerator';
import moment from "moment";
import { AsociarEventoCeluModal } from './AsociarEventoCeluModal';
import { uiOpenModalllll } from '../../actions/ui';

const now = moment();

export const ClienteEditar = () => {
    const history = useHistory();
    const dispatch = useDispatch();
    const qrCodeCanvas = document.querySelector('canvas');
    const dataUrl = qrCodeCanvas&&qrCodeCanvas.toDataURL('image/jpg', 0.3);
    const { uid } = useSelector((state) => state.auth);
    const { waitQR } = useSelector((state) => state.ui);
    const { clienteAsoc } = useSelector((state) => state.clientes);
    const { activeEvent } = useSelector((state) => state.eventos);
    const [formClienteValues, handleClienteInputChange] = useForm({
        cNombre: clienteAsoc ? clienteAsoc.nombre:"",
        cApellido: clienteAsoc ? clienteAsoc.apellido:"",
        cCelular:clienteAsoc ? clienteAsoc.celular:"",
    });
    const { cNombre, cApellido, cCelular } = formClienteValues;
    const handleUpdateCliente = async(e) => {
        e.preventDefault();
        const timer = ms => new Promise(res => setTimeout(res, ms))
        dispatch(uiOpenModalllll());
        for (let i = 0; i < clienteAsoc.entradas.length; i++) {
            await timer(5000);
            if(clienteAsoc.entradas[i].evento===activeEvent){
                dispatch(startUpdateCliente(clienteAsoc.dni, cCelular,now.toDate(), uid,clienteAsoc.entradas[i].nombre,clienteAsoc.entradas[i].tipo+" - $"+clienteAsoc.entradas[i].importe,dataUrl,clienteAsoc.entradas[i].evento));
            }
            // setTimeout(()=>{ dispatch(startUpdateCliente(clienteAsoc.dni, cCelular,now.toDate(), uid,clienteAsoc.entradas[i].nombre,clienteAsoc.entradas[i].tipo+" - $"+clienteAsoc.entradas[i].importe,dataUrl,clienteAsoc.entradas[i].evento));},5000);
            await timer(5000);
        }
        setTimeout(()=>{
            history.goBack();
        },500);
    };
    const handleVolver = (e) => {
        history.push('/');
    };
  return (
  <div>
    
    <div className='container'>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
            <label className='form-label h3'><b>DNI</b></label>
            {clienteAsoc && <h3 className='text-dark'>{clienteAsoc.dni}</h3>}
        </div>
        <form onSubmit={handleUpdateCliente}>
        {/* <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
        <label className='form-label h5'><b>Nombre</b></label>
            <div><input className='border rounded-pill text-center form-control' name="cNombre" disabled type="text" value={cNombre} /></div>
        </div>
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-1'>
        <label className='form-label h5'><b>Apellido</b></label>
            <div><input className='border rounded-pill text-center form-control' name="cApellido" disabled type="text" value={cApellido} /></div>
        </div> */}
        <div className='form-group d-flex justify-content-center align-items-center flex-column mb-3'>
        <label className='form-label h5'><b>Celular</b></label>
            <div><input className='border rounded-pill text-center form-control' name="cCelular" type="text" value={cCelular} onChange={handleClienteInputChange}/></div>
        </div>
        {waitQR===false&& <div className="form-group d-flex justify-content-center mb-2">
            <button type="submit" className="btn btn-success" ><i className="fas fa-check"></i> Actualizar</button>
        </div>}
        </form>
        <div className="form-group d-flex justify-content-center">
    <button className="btn btn-primary mb-5" onClick={handleVolver}><i className="fas fa-caret-square-left"></i> Volver</button>
  </div>
    </div>
    <QRGenerator uid={uid} dni={clienteAsoc.dni} celular={cCelular}/>
    <AsociarEventoCeluModal />
  </div>
  );
};
